import getEquitiesService from '@/services/simulator/get-equities-service';
import getFiveCardHandPercentagesService from '@/services/simulator/get-five-card-hand-percentages-service';

// I'm running the simulations in a worker so that the progress bar works; if I do it in the regular JS runtime, the simulations block the UI from updating; see the question I asked on Stack Overflow
self.addEventListener('message', function (e) {
  if (e.data.message === 'calculate equity') {
    let simulationPlayers = getEquitiesService.constructSimulationPlayers(e.data.weightedRanges);
    let simulationResults = getEquitiesService.constructSimulationResultsContainer(simulationPlayers);

    for (let i = 0; i < e.data.numberOfSimulations; i++) {
      try {
        getEquitiesService.runSimulation(
          simulationPlayers,
          e.data.presetCommunityCardsArray,
          simulationResults
        );
      } catch (e) {
        self.postMessage({
          message: 'error',
          error: e,
        });
        break;
      }

      if (i % (e.data.numberOfSimulations / 100) === 0) {
        self.postMessage({
          message: 'progress',
          value: (i / (e.data.numberOfSimulations / 100)),
        });
      }
    }

    self.postMessage({
      message: 'finished',
      simulationResults: simulationResults,
    });
  }

  else if (e.data.message === 'calculate short deck equity') {
    let simulationPlayers = getEquitiesService.constructSimulationPlayers(e.data.weightedRanges);
    let simulationResults = getEquitiesService.constructSimulationResultsContainer(simulationPlayers);

    for (let i = 0; i < e.data.numberOfSimulations; i++) {
      try {
        getEquitiesService.runShortDeckSimulation(
          simulationPlayers,
          e.data.presetCommunityCardsArray,
          simulationResults
        );
      } catch (e) {
        self.postMessage({
          message: 'error',
          error: e,
        });
        break;
      }

      if (i % (e.data.numberOfSimulations / 100) === 0) {
        self.postMessage({
          message: 'progress',
          value: (i / (e.data.numberOfSimulations / 100)),
        });
      }
    }

    self.postMessage({
      message: 'finished',
      simulationResults: simulationResults,
    });
  }

  else if (e.data.message === 'get five card hand percentages via simulations') {
    let handAndDrawInfo = [];
    let currHandAndDrawInfo;

    for (let i = 0; i < e.data.numberOfSimulations; i++) {
      try {
        currHandAndDrawInfo = getFiveCardHandPercentagesService.runSimulation(
          e.data.weightedRange,
          e.data.presetCommunityCardsArray,
          e.data.streetToSimulateTo,
        );
        handAndDrawInfo.push(currHandAndDrawInfo);
      } catch (e) {
        self.postMessage({
          message: 'error',
          error: e,
        });
        break;
      }

      if (i % (e.data.numberOfSimulations / 100) === 0) {
        self.postMessage({
          message: 'progress',
          value: (i / (e.data.numberOfSimulations / 100)),
        });
      }
    }

    self.postMessage({
      message: 'finished',
      handAndDrawInfo: handAndDrawInfo,
    });
  }

  else if (e.data.message === 'get five card hand percentages via short deck simulations') {
    let handAndDrawInfo = [];
    let currHandAndDrawInfo;

    for (let i = 0; i < e.data.numberOfSimulations; i++) {
      try {
        currHandAndDrawInfo = getFiveCardHandPercentagesService.runShortDeckSimulation(
          e.data.weightedRange,
          e.data.presetCommunityCardsArray,
          e.data.streetToSimulateTo,
        );
        handAndDrawInfo.push(currHandAndDrawInfo);
      } catch (e) {
        self.postMessage({
          message: 'error',
          error: e,
        });
        break;
      }

      if (i % (e.data.numberOfSimulations / 100) === 0) {
        self.postMessage({
          message: 'progress',
          value: (i / (e.data.numberOfSimulations / 100)),
        });
      }
    }

    self.postMessage({
      message: 'finished',
      handAndDrawInfo: handAndDrawInfo,
    });
  }

  else if (process.env.NODE_ENV !== 'testing') {
    throw new Error('Invalid message sent to run-simulations.worker.js.');
  }
});



