import _ from 'lodash';
import constants from '@/services/constants';

export default class Deck {
  constructor() {
    this.cards = [];

    ['a', 'k', 'q', 'j', 't', '9', '8', '7', '6'].forEach(function (rank) {
      constants.suits.forEach(function (suit) {
        this.cards.push(rank + suit);
      }, this);
    }, this);
    this._shuffle(this.cards);
  };

  _shuffle(array) { // from stackoverflow
    let currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  };

  dealRandomCard() {
    if (this.cards.length === 0) {
      throw new Error('Trying to deal from an empty deck.');
    }

    return this.cards.pop();
  };
  dealSpecificCard(cardToDeal) {
    let indexOfCardToDeal = this.cards.indexOf(cardToDeal);

    if (indexOfCardToDeal === -1) {
      throw new Error("Trying to deal a card that isn't in the deck");
    }

    return this.cards.splice(indexOfCardToDeal, 1)[0];
  };
  dealHandFromRange(range) {
    let selectedHand;
    let randomHandFromRange;
    let attempts = 0;

    while (attempts < 1000) {
      randomHandFromRange = _.sample(range);
      attempts++;

      if (this.hasHand(randomHandFromRange)) {
        selectedHand = randomHandFromRange;
        break;
      }
    }

    if (attempts === 1000) {
      throw new Error('Issue trying to deal hand from range.');
    }

    return [
      this.dealSpecificCard(selectedHand[0]),
      this.dealSpecificCard(selectedHand[1]),
    ];
  };
  hasHand(hand) {
    let containsFirstCard;
    let containsSecondCard;

    containsFirstCard = this.cards.indexOf(hand[0]) > -1;

    if (!containsFirstCard) {
      return false;
    }

    containsSecondCard = this.cards.indexOf(hand[1]) > -1;

    if (!containsSecondCard) {
      return false;
    }
    return true;
  };
};



