import utilities from '@/services/utilities/utilities';

export default {
  run: function (handOneInfo, handTwoInfo) {
    if (handOneInfo.value > handTwoInfo.value) {
      return 'hand one';
    }

    else if (handOneInfo.value < handTwoInfo.value) {
      return 'hand two';
    }

    else if (handOneInfo.value === handTwoInfo.value) {
      if (handOneInfo.name === 'royal flush') {
        return this._getWinningRoyalFlush(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'straight flush') {
        return this._getWinningStraightFlush(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'four of a kind') {
        return this._getWinningFourOfAKind(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'full house') {
        return this._getWinningFullHouse(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'flush') {
        return this._getWinningFlush(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'straight') {
        return this._getWinningStraight(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'three of a kind') {
        return this._getWinningThreeOfAKind(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'two pair') {
        return this._getWinningTwoPair(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'pair') {
        return this._getWinningPair(handOneInfo, handTwoInfo);
      } else if (handOneInfo.name === 'high card') {
        return this._getWinningHighCard(handOneInfo, handTwoInfo);
      } else {
        throw new Error('Invalid value of handOneInfo.name.');
      }
    }

    else {
      throw new Error('Invalid value of handOneInfo.value and/or handTwoInfo.value.');
    }
  },
  _getWinningRoyalFlush: function (handOneInfo, handTwoInfo) {
    return 'tie';
  },
  _getWinningStraightFlush: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.highCardRank, handTwoInfo.highCardRank ]
    ]);
  },
  _getWinningFourOfAKind: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.rank, handTwoInfo.rank ],
      [ handOneInfo.kicker[0], handTwoInfo.kicker[0] ],
    ]);
  },
  _getWinningFullHouse: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.tripsRank, handTwoInfo.tripsRank ],
      [ handOneInfo.pairRank, handTwoInfo.pairRank ],
    ]);
  },
  _getWinningFlush: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.ranks[0], handTwoInfo.ranks[0] ],
      [ handOneInfo.ranks[1], handTwoInfo.ranks[1] ],
      [ handOneInfo.ranks[2], handTwoInfo.ranks[2] ],
      [ handOneInfo.ranks[3], handTwoInfo.ranks[3] ],
      [ handOneInfo.ranks[4], handTwoInfo.ranks[4] ],
    ]);
  },
  _getWinningStraight: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.highCardRank, handTwoInfo.highCardRank ],
    ]);
  },
  _getWinningThreeOfAKind: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.rank, handTwoInfo.rank ],
      [ handOneInfo.kickers[0][0], handTwoInfo.kickers[0][0] ],
      [ handOneInfo.kickers[1][0], handTwoInfo.kickers[1][0] ],
    ]);
  },
  _getWinningTwoPair: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.higherPairRank, handTwoInfo.higherPairRank ],
      [ handOneInfo.lowerPairRank, handTwoInfo.lowerPairRank ],
      [ handOneInfo.kicker[0], handTwoInfo.kicker[0] ],
    ]);
  },
  _getWinningPair: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.rank, handTwoInfo.rank ],
      [ handOneInfo.kickers[0][0], handTwoInfo.kickers[0][0] ],
      [ handOneInfo.kickers[1][0], handTwoInfo.kickers[1][0] ],
      [ handOneInfo.kickers[2][0], handTwoInfo.kickers[2][0] ],
    ]);
  },
  _getWinningHighCard: function (handOneInfo, handTwoInfo) {
    return this.__getWinnerUsingRanksAsTiebreaker([
      [ handOneInfo.kickers[0][0], handTwoInfo.kickers[0][0] ],
      [ handOneInfo.kickers[1][0], handTwoInfo.kickers[1][0] ],
      [ handOneInfo.kickers[2][0], handTwoInfo.kickers[2][0] ],
      [ handOneInfo.kickers[3][0], handTwoInfo.kickers[3][0] ],
      [ handOneInfo.kickers[4][0], handTwoInfo.kickers[4][0] ],
    ]);
  },
  __getWinnerUsingRanksAsTiebreaker: function (tiebreakers) {
    for (let i = 0; i < tiebreakers.length; i++) {
      if (utilities.ranks.isFirstRankLargerThanSecond(tiebreakers[i][0], tiebreakers[i][1])) {
        return 'hand one';
      }

      else if (utilities.ranks.isFirstRankLargerThanSecond(tiebreakers[i][1], tiebreakers[i][0])) {
        return 'hand two';
      }

      else if (tiebreakers[i][0] === tiebreakers[i][1]) {
        if (i === (tiebreakers.length - 1)) { // we're on the last tiebreaker
          return 'tie';
        }

        else {
          // there is a tie, so move on to the next tiebreaker
        }
      }

      else {
        throw new Error('Invalid comparison of tiebreaker ranks.');
      }
    }
  },
};



