import utilities from '@/services/utilities/utilities';
import getHandInfo from '@/services/simulator/get-info-from-combined-cards/get-hand-info';
import setSpecificHandInfo from '@/services/simulator/get-info-from-combined-cards/set-specific-hand-info';
import getDrawInfo from '@/services/simulator/get-info-from-combined-cards/get-draw-info';
import Deck from '@/services/simulator/Deck';
import ShortDeck from '@/services/simulator/ShortDeck';
import _ from 'lodash';

export default {
  runSimulation: function (weightedRange, presetCommunityCardsArray, simulateTo) {
    let deck = new Deck();
    let communityCards = [];
    let handInfo;
    let drawInfo = null;
    let simulationPlayer = {
      weightedRange: weightedRange,
      holeCards: null,
    };

    this._dealPresetCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._dealSimulationPlayer(simulationPlayer, deck);
    this._dealRemainingCommunityCards(communityCards, presetCommunityCardsArray, deck, simulateTo);
    handInfo = getHandInfo.run(_.union(simulationPlayer.holeCards, communityCards));
    setSpecificHandInfo.run(handInfo, simulationPlayer.holeCards, communityCards);

    if (simulateTo !== 'river') {
      drawInfo = getDrawInfo.run(simulationPlayer.holeCards, communityCards, handInfo, true);
    }

    return {
      handInfo: handInfo,
      drawInfo: drawInfo,
    };
  },
  _getPresetCommunityCardsArrayUpToSimulateTo: function (presetCommunityCardsArray, simulateTo) {
    if (simulateTo === 'flop' && presetCommunityCardsArray.length >= 3) {
      return presetCommunityCardsArray.slice(0, 3);
    } else if (simulateTo === 'turn' && presetCommunityCardsArray.length >= 4) {
      return presetCommunityCardsArray.slice(0, 4);
    } else if (simulateTo === 'river' && presetCommunityCardsArray.length === 5) {
      return presetCommunityCardsArray;
    } else {
      throw new Error('Invalid arguments for _getPresetCommunityCardsArrayUpToSimulateTo.');
    }
  },
  _dealPresetCommunityCards: function (communityCards, presetCommunityCardsArray, deck) {
    presetCommunityCardsArray.forEach(function (presetCommunityCard) {
      communityCards.push(deck.dealSpecificCard(presetCommunityCard));
    });
  },
  _dealSimulationPlayer: function (simulationPlayer, deck) {
    simulationPlayer.holeCards = deck.dealHandFromRange(simulationPlayer.weightedRange);
  },
  _dealRemainingCommunityCards: function (communityCards, presetCommunityCardsArray, deck, simulateTo) {
    let numCommunityCardsLeftToDeal = this.__getNumCommunityCardsLeftToDeal(simulateTo, presetCommunityCardsArray);

    _.times(numCommunityCardsLeftToDeal, function () {
      communityCards.push(deck.dealRandomCard());
    });
  },
  __getNumCommunityCardsLeftToDeal: function (simulateTo, presetCommunityCardsArray) {
    let numPresetCommunityCards = presetCommunityCardsArray.length;

    if (simulateTo === 'flop') {
      return 3 - numPresetCommunityCards;
    } else if (simulateTo === 'turn') {
      return 4 - numPresetCommunityCards;
    } else if (simulateTo === 'river') {
      return 5 - numPresetCommunityCards;
    } else {
      throw new Error('Invalid value for simulateTo.');
    }
  },

  runShortDeckSimulation: function (weightedRange, presetCommunityCardsArray, simulateTo, simulationResults) {
    let deck = new ShortDeck();
    let communityCards = [];
    let handInfo;
    let drawInfo = null;
    let simulationPlayer = {
      weightedRange: weightedRange,
      holeCards: null,
    };

    this._dealPresetCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._dealSimulationPlayer(simulationPlayer, deck);
    this._dealRemainingCommunityCards(communityCards, presetCommunityCardsArray, deck, simulateTo);
    handInfo = getHandInfo.runForShortDeck(_.union(simulationPlayer.holeCards, communityCards));
    setSpecificHandInfo.run(handInfo, simulationPlayer.holeCards, communityCards, true);

    if (simulateTo !== 'river') {
      drawInfo = getDrawInfo.run(simulationPlayer.holeCards, communityCards, handInfo, true, true);
    }

    return {
      handInfo: handInfo,
      drawInfo: drawInfo,
    };
  },
};



