import Deck from '@/services/simulator/Deck';
import ShortDeck from '@/services/simulator/ShortDeck';
import getHandInfo from '@/services/simulator/get-info-from-combined-cards/get-hand-info';
import getWinningHand from '@/services/simulator/get-winning-hand';
import _ from 'lodash';

export default {
  constructSimulationPlayers: function (weightedRanges) {
    let simulationPlayers = [];

    weightedRanges.forEach(function (weightedRange) {
      simulationPlayers.push({
        holeCards: null,
        weightedRange: weightedRange,
        oneSpecificHandInWeightedRange: weightedRange.length === 1,
      });
    });

    return simulationPlayers;
  },
  constructSimulationResultsContainer: function (players) {
    let simulationResultsContainer = [];

    players.forEach(function () {
      simulationResultsContainer.push({
        wins: 0,
        ties: 0,
        winFractions: 0,
      });
    });

    return simulationResultsContainer;
  },
  runSimulation: function (simulationPlayers, presetCommunityCardsArray, simulationResults) {
    let deck = new Deck();
    let communityCards = [];

    this._dealPresetCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._dealSimulationPlayers(simulationPlayers, deck);
    this._dealRemainingCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._setHandInfoOnEachSimulationPlayer(simulationPlayers, communityCards);
    this._incrementResults(simulationPlayers, simulationResults);
  },
  _dealPresetCommunityCards: function (communityCards, presetCommunityCardsArray, deck) {
    presetCommunityCardsArray.forEach(function (presetCommunityCard) {
      communityCards.push(deck.dealSpecificCard(presetCommunityCard));
    });
  },
  _dealSimulationPlayers: function (simulationPlayers, deck) {
    this.__dealSimulationPlayersThatHaveOneSpecificHand(simulationPlayers, deck);
    this.__dealSimulationPlayersThatHaveARange(simulationPlayers, deck);

    if (!this.__checkIfAllSimulationPlayersHaveBeenDealt(simulationPlayers)) {
      throw new Error('Simulation player ' + i + ' wasn\'t dealt.');
    }
  },
  __dealSimulationPlayersThatHaveOneSpecificHand: function (simulationPlayers, deck) {
    let simulationPlayersThatHaveOneSpecificHand = simulationPlayers.filter(function (simulationPlayer) {
      return simulationPlayer.oneSpecificHandInWeightedRange;
    });

    simulationPlayersThatHaveOneSpecificHand.forEach(function (simulationPlayer, i) {
      simulationPlayer.holeCards = deck.dealHandFromRange(simulationPlayer.weightedRange);
    });
  },
  __dealSimulationPlayersThatHaveARange: function (simulationPlayers, deck) {
    let simulationPlayersThatHaveARange = simulationPlayers.filter(function (simulationPlayer) {
      return !simulationPlayer.oneSpecificHandInWeightedRange;
    });;

    simulationPlayersThatHaveARange.forEach(function (simulationPlayer, i) {
      simulationPlayer.holeCards = deck.dealHandFromRange(simulationPlayer.weightedRange);
    });
  },
  __checkIfAllSimulationPlayersHaveBeenDealt: function (simulationPlayers) {
    for (let i = 0; i < simulationPlayers.length; i++) {
      if (simulationPlayers[i].holeCards === null) {
        return false;
      }
    }

    return true;
  },
  _dealRemainingCommunityCards: function (communityCards, presetCommunityCardsArray, deck) {
    let numCommunityCardsLeftToDeal = 5 - presetCommunityCardsArray.length;

    _.times(numCommunityCardsLeftToDeal, function () {
      communityCards.push(deck.dealRandomCard());
    });
  },
  _setHandInfoOnEachSimulationPlayer: function (simulationPlayers, communityCards) {
    let cards;

    simulationPlayers.forEach(function (simulationPlayer) {
      cards = _.union(simulationPlayer.holeCards, communityCards);
      simulationPlayer.handInfo = getHandInfo.run(cards);
    });
  },
  _incrementResults: function (simulationPlayers, simulationResults) {
    let winningHandInfo = this.__getWinningHandInfo(simulationPlayers);
    let numberOfWinners = this.__getNumberOfWinners(winningHandInfo, simulationPlayers);

    simulationPlayers.forEach(function (simulationPlayer, i) {
      if (getWinningHand.run(simulationPlayer.handInfo, winningHandInfo) === 'tie') {
        if (numberOfWinners === 1) {
          simulationResults[i].wins++;
        } else if (numberOfWinners > 1) {
          simulationResults[i].ties++;
          simulationResults[i].winFractions += (1 / numberOfWinners);
        }
      }
    });
  },
  __getWinningHandInfo: function (simulationPlayers) {
    let winningHandInfo = simulationPlayers[0].handInfo;

    for (let i = 1; i < simulationPlayers.length; i++) {
      if (getWinningHand.run(simulationPlayers[i].handInfo, winningHandInfo) === 'hand one') {
        winningHandInfo = simulationPlayers[i].handInfo;
      }
    }

    return winningHandInfo;
  },
  __getNumberOfWinners: function (winningHandInfo, simulationPlayers) {
    let numberOfWinners = 0;

    simulationPlayers.forEach(function (simulationPlayer) {
      if (getWinningHand.run(simulationPlayer.handInfo, winningHandInfo) === 'tie') {
        numberOfWinners++;
      }
    });

    return numberOfWinners;
  },

  runShortDeckSimulation: function (simulationPlayers, presetCommunityCardsArray, simulationResults) {
    let deck = new ShortDeck();
    let communityCards = [];

    this._dealPresetCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._dealSimulationPlayers(simulationPlayers, deck);
    this._dealRemainingCommunityCards(communityCards, presetCommunityCardsArray, deck);
    this._setHandInfoOnEachShortDeckSimulationPlayer(simulationPlayers, communityCards);
    this._incrementShortDeckResults(simulationPlayers, simulationResults);
  },
  _setHandInfoOnEachShortDeckSimulationPlayer: function (simulationPlayers, communityCards) {
    let cards;

    simulationPlayers.forEach(function (simulationPlayer) {
      cards = _.union(simulationPlayer.holeCards, communityCards);
      simulationPlayer.handInfo = getHandInfo.runForShortDeck(cards);
    });
  },
  _incrementShortDeckResults: function (simulationPlayers, simulationResults) {
    let winningHandInfo = this.__getShortDeckWinningHandInfo(simulationPlayers);
    let numberOfWinners = this.__getShortDeckNumberOfWinners(winningHandInfo, simulationPlayers);

    simulationPlayers.forEach(function (simulationPlayer, i) {
      if (getWinningHand.run(simulationPlayer.handInfo, winningHandInfo) === 'tie') {
        if (numberOfWinners === 1) {
          simulationResults[i].wins++;
        } else if (numberOfWinners > 1) {
          simulationResults[i].ties++;
          simulationResults[i].winFractions += (1 / numberOfWinners);
        }
      }
    });
  },
  __getShortDeckWinningHandInfo: function (simulationPlayers) {
    let winningHandInfo = simulationPlayers[0].handInfo;

    for (let i = 1; i < simulationPlayers.length; i++) {
      if (getWinningHand.run(simulationPlayers[i].handInfo, winningHandInfo) === 'hand one') {
        winningHandInfo = simulationPlayers[i].handInfo;
      }
    }

    return winningHandInfo;
  },
  __getShortDeckNumberOfWinners: function (winningHandInfo, simulationPlayers) {
    let numberOfWinners = 0;

    simulationPlayers.forEach(function (simulationPlayer) {
      if (getWinningHand.run(simulationPlayer.handInfo, winningHandInfo) === 'tie') {
        numberOfWinners++;
      }
    });

    return numberOfWinners;
  },
};



